                v-model="listLoading"
                :finished="finished"
                loading-text='Loading...'
                @load="getData(selectClassTypeId)"
            >
                <template v-if="listData.length > 0">
                    <div class="whiteItemBox" v-for="(item,index) in listData" :key="index"  @click="goPage('/MovieDetails/'+item.id)">
