                            </div>
                            <div class="todayMsgBox flexBetween">
                                <div class="todayMsgItem flexCenter">
                                    <p class="msgNumber">{{moneySymbol+'--'}}</p>
                                    <p class="msgTitle">Rent paid</p>
                                </div>
                                <div class="todayMsgItem flexCenter">
                                    <p class="msgNumber">{{moneySymbol+'--'}}</p>
                                    <p class="msgTitle">Video earned</p>
                                </div>
                                <div class="todayMsgItem flexCenter">
                                    <p class="msgNumber">{{moneySymbol+'--'}}</p>
                                    <p class="msgTitle">Subscription</p>
                                </div>
                            </div>
                        </div>
                        <div class="itemTodayBox flexStart">
                            <div class="todayBox flexCenter">
                                <img class='todayIcon' src="../assets/images/totalIcon.png" alt="">
                                <p class="todayText">Total</p>
                            </div>
                            <div class="todayMsgBox flexBetween">
                                <div class="todayMsgItem flexCenter">
                                    <p class="msgNumber">{{moneySymbol+'--'}}</p>
                                    <p class="msgTitle">Rent paid</p>
                                </div>
                                <div class="todayMsgItem flexCenter">
                                    <p class="msgNumber">{{moneySymbol+'--'}}</p>
                                    <p class="msgTitle">Video earned</p>
                                </div>
                                <div class="todayMsgItem flexCenter">
